.button-up {
  z-index: 200;
  color: #fff;
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: rgba(212, 20, 68, .333);
  border-radius: 50%;
  justify-content: center;
  transition: all .3s linear;
  display: flex;
  position: fixed;
  bottom: 20px;
  right: 20px;
